

























































import loadMore from '../../components/load-more.vue'
import uniNavBar from '../../components/uni-nav-bar.vue'
import {mapState,mapMutations} from 'vuex'
var util = require('../../common/util.js');
var formatLocation = util.formatLocation;
export default { 
	data() {
		return {
			loadingType: 0, 
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			items:[
				{
					'name':'【驾图】产品服务即可加入车联网',
					'img':'../../static/item_1.png'
				},
				{
					'name':'蜜糖卡密激活码',
					'img':'../../static/item_2.png'
				},
				{
					'name':'【驾图】产品服务即可加入车联网',
					'img':'../../static/item_1.png'
				},
				{
					'name':'蜜糖卡密激活码',
					'img':'../../static/item_2.png'
				}
			],
			screenHeight : '',
			lunbo_img:['../../static/1.png','../../static/2.jpg','../../static/3.jpg'],
			indicatorDots: true,//显示指示点
			autoplay: true,
			interval: 3000,
			duration: 1000,
			locationAddress:'位置'
		}
	},
	computed: mapState(['hasLogin','isLogin', 'username','isGesturePW']), // 获取Vuex仓库里面的状态值
	onLoad() {
		// 首次加载的时候判断用户名是否存在，如果存在，将该用户名存于vuex中
		let name = uni.getStorageSync('username');
		if(name){
			this.setUsername(name);
		}
	},
	onShow() {
		if(this.username){
			uni.setStorageSync('username',this.username);// 将用户名缓存，下次登录的时候用
		}
	},
	onReachBottom() { //上拉加载方法
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		setTimeout(() => {
			if (this.items.length > 26) {
				this.loadingType = 2;
				return;
			}
			this.items.push({
				'name':'【驾图】产品服务即可加入车联网',
				'img':'../../static/item_1.png'
			},{
				'name':'【驾图】产品服务即可加入车联网1',
				'img':'../../static/item_1.png'
			})
			this.loadingType = 0;
		}, 800);
	},
	onPullDownRefresh() { //监听下拉刷新
		let _this = this;
		setTimeout(function(){
			uni.stopPullDownRefresh();
			if(_this.items.length > 26){
				uni.showToast({
					title:"没有新内容了",
					icon:"none",
				});
				return;
			}else{
				_this.items.push({
					'name':'【驾图】产品服务即可加入车联网',
					'img':'../../static/item_1.png'
				})
			}
		},2000)
	},
	mounted:function(){ 
		this.$nextTick(function(){
			this.getHeight();
		})
	},
	methods: {
		...mapMutations(['setUsername']), // 触发vuex中mutation下的setUsername方法，把获取到的缓存的用户名存于vue中
		clickItem(index){ // 点击功能菜单
			if (!this.hasLogin && this.isGesturePW == false) { // 判断登录状态及手势密码是否开启
				uni.navigateTo({
					url: '../login/login'
				});
			}else if(!this.hasLogin && this.isGesturePW == true){
				// 如果有账号(缓存记录)，但是没有登录，则显示手势登录页面
				if(this.username){
					uni.navigateTo({
						url:'../gesture-lock/gesture-lock'
					});
				}else{
					uni.navigateTo({
						url: '../login/login'
					});
				}
			}
		},
		goSlideview(){
			uni.navigateTo({
				url:'../API/textWeex/textWeex'
			})
		},
		scanCode(){
			uni.scanCode({
				onlyFromCamera:false,//是否只能从相机扫码
				success(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			})
		},
		chooseLocation(){ // 接收子组件的点击事件状态
			uni.chooseLocation({
				success: (res) => {
					this.hasLocation = true,
					this.location = formatLocation(res.longitude, res.latitude),
					this.locationAddress = res.address
				}
			})
		},
		getHeight() {
			let _this = this;
			uni.getSystemInfo({
				success:function(res){
					_this.screenHeight = res.screenHeight; // 获取手机屏幕高度
				}
			})
		}, 
	},
	components: {
		loadMore,
		uniNavBar
	}
}



